














import {Component, Prop, Vue, Watch} from "vue-property-decorator";
import DebtModel from "@/models/1c/Debt";
import DateTypeFilters from "@/components/parts/index/DateTypeFilters.vue";
import RealtyObject from "@/models/1c/RealtyObject";


@Component({
  components: {DateTypeFilters}
})
export default class Debt extends Vue {
  @Prop({ type: Object, required: true }) billTypes!: { [key: number]: string }
  @Prop({ type: Object, required: true }) currentRealty!: RealtyObject

  items = [] as Array<DebtModel>
  formData = {} as { to: string, from: string, type: string, object_id: string }
  columns = [
    {
      key: 'type',
      label: 'Вид счета'
    },
    {
      key: 'date',
      label: 'За дату'
    },
    {
      key: 'value',
      label: 'Показание'
    },
    {
      key: 'actions',
      label: '  '
    }
  ]

  updateItems(): void {
    DebtModel.getList({
      ...this.formData,
      object_id: this.currentRealty.id_1c as number
    }).then(res => {
      this.items = res.data
    })
  }

  @Watch('currentRealty', { immediate: true })
  watchCurrentRealty(): void {
    this.updateItems()
  }
  @Watch('formData', { deep: true })
  watchFormData(): void {
    this.updateItems()
  }
}
